<script setup>
import { ArrowLeft } from 'lucide-vue-next';
import { ArrowRight } from 'lucide-vue-next';
</script>

<template>
  <div class="carousel-wrapper">
    <button
      class="arrow left"
      @click="scrollLeft"
      :disabled="isAnimating"
      :class="{ active: isLeftActive }"
      @mousedown="isLeftActive = true"
      @mouseup="isLeftActive = false"
      @mouseleave="isLeftActive = false"
    >
      <ArrowLeft :size="24" :stroke-width="2" />
    </button>
    <div class="gallery-container" ref="galleryContainer" @scroll="handleScroll">
      <div
        v-for="(image, idx) in images"
        :key="idx + '-' + image.id"
        class="gallery-item"
      >
        <img :src="image.url" :alt="image.alt" />
      </div>
    </div>
    <button
      class="arrow right"
      @click="scrollRight"
      :disabled="isAnimating"
      :class="{ active: isRightActive }"
      @mousedown="isRightActive = true"
      @mouseup="isRightActive = false"
      @mouseleave="isRightActive = false"
    >
      <ArrowRight :size="24" :stroke-width="2" />
    </button>
  </div>
</template>

<script>
export default {
  name: 'ImageCarousel',
  data() {
    return {
      images: [
        { id: 1, url: 'gallery/blue-pool-robot.JPG', alt: 'Description 1' },
        { id: 3, url: 'gallery/surface-robot.JPG', alt: 'Description 3' },
        { id: 2, url: 'gallery/grass-robot.jpg', alt: 'Description 2' },
        { id: 3, url: 'gallery/internals-robot.JPG', alt: 'Description 3' },
        { id: 3, url: 'gallery/torpedo-robot.JPG', alt: 'Description 3' },
      ],
      isAnimating: false,
      isLeftActive: false,
      isRightActive: false,
    };
  },
  mounted() {
    this.$nextTick(() => {
      const container = this.$refs.galleryContainer;
      const itemWidth = container.querySelector('.gallery-item').offsetWidth + 16; // 16 is your gap
      const startIndex = 2; // Change this to the index you want to start on (0 = first image)
      container.scrollLeft = itemWidth * startIndex;
    });
  },
  methods: {
    scrollLeft() {
      if (this.isAnimating) return;
      this.isAnimating = true;
      const container = this.$refs.galleryContainer;
      const itemWidth = container.querySelector('.gallery-item').offsetWidth + 16;
      const target = Math.max(0, container.scrollLeft - itemWidth);
      container.scrollTo({ left: target, behavior: 'smooth' });
      setTimeout(() => {
        this.isAnimating = false;
      }, 400);
    },
    scrollRight() {
      if (this.isAnimating) return;
      this.isAnimating = true;
      const container = this.$refs.galleryContainer;
      const itemWidth = container.querySelector('.gallery-item').offsetWidth + 16;
      const maxScroll = container.scrollWidth - container.clientWidth;
      const target = Math.min(maxScroll, container.scrollLeft + itemWidth);
      container.scrollTo({ left: target, behavior: 'smooth' });
      setTimeout(() => {
        this.isAnimating = false;
      }, 400);
    },
    handleScroll() {
      // Optional: you can disable arrows at the ends if you want
    },
  },
};
</script>

<style scoped>
.carousel-wrapper {
  position: relative;
  width: 99vw;
  max-width: 99vw;
  display: flex;
  align-items: center;
  justify-content: center; 
  margin: 0;
  padding: 0;
  box-sizing: border-box;
}
.arrow {
  position: absolute;
  top: 50%;
  transform: translateY(-50%);
  z-index: 2;
  background: var(--white, #fff);
  border: 1px solid #ccc;
  border-radius: 50%;
  width: 40px;
  height: 40px;
  font-size: 2em;
  cursor: pointer;
  user-select: none;
  transition: background 0.5s, opacity 0.5s;
  display: flex;
  align-items: center;
  justify-content: center;
  opacity: 0.85;
}
.arrow.left {
  left: 12px;
}
.arrow.right {
  right: 12px;
}
.arrow:hover {
  background: #eee;
}
.arrow.active {
  opacity: 0.4;
  background: var(--white, #fff); /* or adjust as needed */
}
.gallery-container {
  display: flex;
  overflow-x: auto;
  scroll-behavior: smooth;
  gap: 16px;
  padding-bottom: 20px;
  width: 100vw;
  max-width: 100vw;
  box-sizing: border-box;
  scrollbar-width: none;
  -ms-overflow-style: none;
  scroll-snap-type: x mandatory;
  margin: 0;
  padding-left: 40vw;  
  padding-right: 40vw;
}
.gallery-container::-webkit-scrollbar {
  display: none;
}
.gallery-item {
  flex-shrink: 0;
  width: 550px;
  max-width: 80vw;
  /* max-height: 400px; */
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  /* padding: 10px; */
  margin: 0;
  scroll-snap-align: center;
}
.gallery-item img {
  margin: 0;
  padding: 0;
  max-width: 100%;
  width: 100%;
  height: 350px;
  object-fit: cover;   /* Crops but fills the box */
  display: block;
  border-radius: 2%;
  background: #fff;      /* Optional: background color for letterboxing */
}
</style>