<template>
  <div class="google-calendar-container">
    <iframe
      :src="calendarEmbedUrl"
      style="border-width:0"
      width="600"
      height="600"
      scrolling="no"
    ></iframe>
  </div>
</template>

<script>
export default {
  name: 'GoogleCalendarEmbed',
  props: {
    // You can make the calendar ID or URL dynamic if needed
    calendarId: {
      type: String,
      default: 'c_cd6266a79df7397e769762d0f962cc473f2879281dda4c9f4a7c7e49a58c5c49@group.calendar.google.com' // Replace with your actual calendar ID
    },
    // Example of a prop for customizing the view (month, week, agenda)
    view: {
      type: String,
      default: 'month'
    },
    showTitle: {
      type: [Boolean, Number], // Accepts true/false or 1/0
      default: false // Default to hiding the title
    }
  },
  computed: {
    calendarEmbedUrl() {
      // Construct the URL based on your calendar ID and other parameters
      // This is a simplified example, refer to Google Calendar embed documentation for full options
      return `https://calendar.google.com/calendar/embed?src=${this.calendarId}&ctz=America%2FLos_Angeles&mode=${this.view}&showTitle=${this.showTitle}&showPrint=0&`;
    }
  },
  mounted() {
    // Optional: You might want to add a loading spinner or other logic here
    console.log('Google Calendar Embed component mounted.');
    console.log('Calendar Embed URL:', this.calendarEmbedUrl);
  }
};
</script>

<style scoped>
.google-calendar-container {
  /* Add any styling for your container */
  width: 100%;
  max-width: 600px; /* Example max-width */
  margin: 0 auto;
}
iframe {
  width: 100%;
  height: 400px; /* Or a more responsive height */
  border-radius: 2%;
}
</style>