        <div class="list-display">
          <MemberTile v-for="(member, index) in members" :key="index" :data="member"/>
        </div>
      </section>
    </div>
  </section>
</template>

<script>
import LeaderTile from "@/components/LeaderTile.vue";
import MemberTile from "@/components/MemberTile.vue";

export default {
  name: "TeamView",
  components: {
    LeaderTile,
    MemberTile,
  },
  computed: {
