<style lang="scss">
#contact{
  position: relative;
  background-color: var(--white);
  padding: 5%;
  padding-top: 1%;
  padding-bottom: 2%;

  .contact-text {
    font-style: italic;
    // text-size: .8em;
  }

  .icons{
    display: flex;
    flex-flow: row wrap;
    justify-content: center;
    align-items: center;
    gap: clamp(0.5em, 2.2vw, 3em);
    margin-top: 1em;

    svg{
      width: clamp(2em, 5vw, 7em);
      max-height: 4em;
      fill: var(--medium);
      transition: transform 0.1s ease-in-out; // Smooth animation on hover      
    }

    .email, .discord, .instagram, .github, .linkedin{
      display: inline-block;
      &:hover{
        cursor: pointer;
        svg{
          transform: scale(1.1);
          }
      }
    }
  }
}

@media (max-width: 480px) {
  .icons {
    gap: 1em; /* Reduce gap further for smaller screens */
  }

  svg {
    width: clamp(1.5em, 4vw, 5em); /* Adjust scaling for very narrow screens */
  }
}


