// Documentation: https://next.vuex.vuejs.org/

import { createStore } from 'vuex'
import generalData from "@/data/general.json"
import leadersData from "@/data/leaders.json"
import advisorsData from "@/data/advisors.json"
import membersData from "@/data/members.json"
import sponsorsData from "@/data/sponsors.json"

export default createStore({
  state: {
    general: {}, 
    leaders: [],
    advisors: [],
    members: [],
    sponsors: []
  },
  getters: {
    general: state => state.general,
    leaders: state => state.leaders,
    advisors: state => state.advisors,
    members: state => state.members,
    sponsors: state => state.sponsors
  },
  mutations: {
    setGeneral(state, general){
      state.general = general
    },
    setLeaders(state, leaders){
      state.leaders = leaders
    },
    setAdvisors(state, advisors){
      state.advisors = advisors
    },
    setMembers(state, members){
      state.members = members
    },
    setSponsors(state, sponsors){
      state.sponsors = sponsors
    }
  },
  actions: {
    async loadGeneral({commit}){
      commit("setGeneral", generalData)
    }, 
    async loadLeaders({commit}){
      commit("setLeaders", leadersData)
    },
    async loadAdvisors({commit}){
      commit("setAdvisors", advisorsData)
    },
    async loadMembers({commit}){
      commit("setMembers", membersData)
    },
    async loadSponsors({commit}){
      commit("setSponsors", sponsorsData)
    }
  },
  modules: {}
})
